<template>
  <div class="user_data">
    <v-Head :showHeader='showHeader'></v-Head>
    <div class="user">
      <div class="user_header">
        <img class="user_head_image" src="../../assets/images/user_header.png">
        <span class="user_label_name">{{ userRealName }}</span>
      </div>
      <el-menu class="user-el-menu" :default-active="activeIndex"
               text-color="#060001" active-text-color="#4778C7" unique-opened router>
        <template v-for="item in items">
          <el-menu-item :index="item.index" :key="item.index">
            <span class="testChild" slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </el-menu>
    </div>
    <div class="userContentBox">
      <div style="font: 0px/0px sans-serif;clear: both;display: block"> </div>
      <div class="content">
        <transition name="move" mode="out-in">
          <keep-alive :include="tagsList">
            <router-view></router-view>
          </keep-alive>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
  import bus from '../common/bus'
  import vHead from '../common/Header.vue'

  export default {
    name: 'user',
    data () {
      return {
        items: [
          {
            index: 'info',
            title: '个人信息'
          },
          {
            index: 'memberCenter',
            title: '会员中心'
          },
          {
            index: 'data',
            title: '我的数据'
          },
          {
            index: 'memberManagement',
            title: '成员管理'
          },
          {
            index: 'loginLog',
            title: '登录日志'
          },
          {
            index: 'queryRecord',
            title: '查询记录'
          },
          {
            index: 'question',
            title: '修改密保问题'
          },
          {
            index: 'changePwd',
            title: '密码修改'
          },
          {
            index: 'exitLogon',
            title: '返回查询中心'
          }
        ],
        tagsList: [],
        userRealName: '',
        activeIndex: 'info',
        showHeader: false,
      }
    },
    computed: {
      onRoutes () {
        bus.$emit('navTitle', this.$route.meta.title)
        return this.$route.path.replace('/', '')
      }
    },
    created () {
      // 通过 Event Bus 进行组件间通信，来折叠侧边栏
      // bus.$on('collapse', msg => {
      //   this.collapse = msg
      // })
    },
    mounted () {
      bus.$emit('navTitle', this.$route.meta.title)
      this.userRealName = localStorage.getItem('userName')
    },
    components: {
      vHead
    }
  }
</script>

<style>
  .user_data {
    width: 100%;
    height: 100%;
  }
  .user {
    display: block;
    width: 200px;
    box-sizing: border-box;
    margin: 0 auto;
    float: left;
    height: calc(100% - 80px);
  }
  .user::-webkit-scrollbar{
    width: 0;
  }
  .user_header {
    margin: 0 auto;
    padding: 20px 0;
    width: 100%;
    text-align: center;
    border-right: 1px solid #e6e6e6;
    box-sizing: border-box;
  }
  .user .user_header .user_head_image {
    display: block;
    width: 70px;
    height: 70px;
    vertical-align: middle;
    margin: 20px auto;
  }
  .user .user_header .user_label_name {
    text-align: center;
  }
  .user > ul {
    height:100%;
    width: 200px;
    box-sizing: border-box;
  }
  .user ul li {
    text-align: left;
    padding-left: 50px!important;
  }
  .userContentBox {
    background: rgba(240,240,240,1);
    height: calc(100% - 80px);
    padding: 40px;
    float: left;
    display: inline-block;
    width: calc(100% - 200px);
    box-sizing: border-box;
    position: relative;
  }
  .content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 40px 40px 160px;
    box-sizing: border-box;
  }

  .el-menu-item:focus, .el-menu-item:hover {
    background-color: #EDEDED;
  }
</style>



